export const toggleChange = (toggle) => {
  switch (toggle) {
    case true:
      return {
        type: "SET_FALSE",
        payload: false,
      };
    case false:
      return {
        type: "SET_TRUE",
        payload: true,
      };
  }
};

export const toggleFalse = (num) => {
  return {
    type: "SET_TRUE",
    payload: true,
  };
};
